<template>
    
    <div class="container">
        <div class="row">
           <ul class="tab_list">
               <li class="tab_list_li">TO DO</li>
               <li class="tab_list_li">DO</li>
               <li class="tab_list_li">SUPP</li>
           </ul>
        </div>
        <div class="row">
            <div class="div body">
            <div class="col-md-9">
                <div class="contenu">
<h5>Texte : </h5><input type="text" v-model="desc" placeholder="add here ..." >         
<h5>Date :</h5> <input type="date" placeholder="add here ..." v-model="date">    
<button v-on:click='add_todo()'>Add</button>     
       </div> </div>
           <div class="col-md-9">
               <div  class="contenu_todo">
                            <div   v-for="(item , index) in todo" :class="{'show':item.show}"  :key="index" class="do">
                                        <div class="check">
                                        <input class='check' type="checkbox">
                                        </div>
                                        <div class="title">{{item.title_todo}}</div>
                                        <div class="date">{{item.date_todo}}</div>
                        <div class="feature-icon">
                     <i v-if='!item.favorite_todo' class="far fa-star" v-on:click= "item.favorite_todo= !item.favorite_todo"></i>
                        <i v-else class="fa fa-star" v-on:click= "item.favorite_todo= !item.favorite_todo"></i> 
                        </div>                            <div class="sup"><i class="far fa-calendar-times"></i></div>
                            </div>
                        </div>
                    <div v-if="!todo[0]" class="contenu_todo">is empty</div>
                </div>
            </div>

        </div>
    </div>

</template>
<script>
import axios from "axios";
import { server } from "../helper";
export default {
      data:()=>{
    return{
todo:[],
desc:'',
date:'',
favorite: false,

    }
    },
    methods:{
        add_todo(){
            if(this.title !== '') {
                console.log(this.desc);
                console.log(this.date);
           let newtodo = {
               id:3,
           title_todo:this.desc,
           date_todo:this.date,
           favorite_todo :this.favorite,
           show: false,
            }
            if (newtodo.favorite_todo){
            this.todo.splice(0,0,newtodo);}
            else{this.todo.push(newtodo); }
     setTimeout(() => {this.todo.find(element => element.date_todo === newtodo.date_todo).show = true;
        }, 0);
this.desc='';
this.favorite=false;
      axios.post(`${server.baseURL}/todo/create`, newtodo).then(response => (console.log(response)));

           }
      

        },


    }
    
    }

</script>
<style>
.date{
    width: 20%;
}
.title{
    width:100%;
}

.fa-calendar-times{
color:rgb(255, 43, 43);
}
.feature-icon{
    margin-left: 20px;
    margin-right: 10px;
}
.check{
        width: 100px;
    height: 20px;
}
.contenu_todo .do{
    margin-bottom: 10px;
      opacity: 1;
  transition: all 0.8s linear;
    border-radius: 20px;
    padding: 10px;
    display:flex;
      border-color: #f95d96;
    border-style: groove;
}
.contenu_todo .do .show{
    opacity: 1 !important;
}
.body{
    width: 90%;
    background-color: rgb(255, 255, 255);
    margin-left: 20px;
    border-radius: 20px;
}
.tab_list{
    width: 90%;
    border-radius: 20px;
    background: #f95d96;
    list-style: outside none none;
    display: inline-flex;
    margin: 20px;
}
.contenu{
    padding: 10px;
  width: 122%;
    border-radius: 20px;
    background: #f95d96;
    list-style: outside none none;
    display: inline-flex;
    margin: 20px;
}
.tab_list_li{
margin-top: 15px;
    margin-left: 200px;
    margin-bottom: 10px;
    font-family: cursive;
    color: aliceblue;
}

body{
background-color: #ffc5f7
}
h5{    font-family: cursive;

    margin-left:50px;
    margin-right: 50px;
}
button{
    margin-left: 40px;
    border-radius: 20px;
    background-color: #ffc5f7;
    border-color: #ffc5f7;
}
input{
        font-family: cursive;

    border-radius: 20px;
}
.contenu_todo{
    width: 122%;
    border-radius: 20px;
    margin-top: 15px;
    margin-left: 20px;
    margin-bottom: 10px;
    font-family: cursive;
}

</style>